<template>
  <div id="app">
    <main v-if="!$route.meta.hideHeader">
      <HeaderAdmin
        v-if="
          isLoggedIn &&
          loginInfo.user_is_admin &&
          !loginInfo.user_is_manager &&
          isChecked
        "
        class="fixed-header"
      />
      <HeaderManager
        v-else-if="
          isLoggedIn &&
          !loginInfo.user_is_admin &&
          loginInfo.user_is_manager &&
          isChecked
        "
        class="fixed-header"
      />
      <HeaderUser v-else-if="isLoggedIn && !isChecked" class="fixed-header" />

      <router-view />

      <button @click="scrollToTop" class="top-button">
        <i class="bi bi-chevron-up"></i>
      </button>
      <Footer :key="$route.fullPath" />
      <!-- TOP  -->
    </main>

    <router-view v-else />
    <!-- hideHeader true  main Header  router-view  -->
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import HeaderAdmin from "./views/common/HeaderAdminView.vue";
import HeaderUser from "./views/common/HeaderUserView.vue";
import HeaderManager from "./views/common/HeaderManagerView.vue";
import Footer from "./views/common/FooterView.vue";

export default {
  name: "App",
  data() {
    return {
      isModalOpen: false,
    };
  },
  components: {
    HeaderAdmin,
    HeaderUser,
    HeaderManager,
    Footer,
  },
  computed: {
    ...mapGetters("login", ["getLoginInfo", "getIsChecked"]),
    loginInfo() {
      return this.getLoginInfo;
    },
    isChecked() {
      return this.getIsChecked;
    },
    isLoggedIn() {
      return !!this.loginInfo; // loginInfo null    .
    },
  },
  watch: {
    $route: {
      handler() {
        //        .
        console.log("Route changed");
        //      
        console.log("isLoggedIn:", this.isLoggedIn);
        console.log("loginInfo:", this.loginInfo);
        console.log("isChecked:", this.isChecked);
      },
      immediate: true, //      
    },
  },
  methods: {
    scrollToTop() {
      console.log("scrollToTop ");
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
  },
};
</script>

<style>
@import url("./assets/css/css.css");
</style>
